<template>
  <v-dialog persistent v-model="registerDialog">
    <v-btn primary accent slot="activator">
      {{ ' Izbriši događaj' }}
    </v-btn>
    <v-card>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-title>Brisanje događaja</v-card-title>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>Jeste li sigurni da želite izbrisati događaj?</v-card-text>
          </v-flex>
        </v-layout>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn
                class="red--text darken-1"
                flat
                @click="registerDialog = false">Odustani</v-btn>
              <v-btn
                class="green--text darken-1"
                flat
                @click="onAgree">Potvrdi</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props: ['event'],
    data () {
      return {
        registerDialog: false
      }
    },
    computed: {
      eventpodaci () {
        return this.event.id
      }
    },
    methods: {
      onAgree () {
        this.$store.dispatch('DeleteEvent', this.event.id)
        this.$store.dispatch('loadPastEvents')
        this.$store.dispatch('loadEvents')
        this.$store.dispatch('loadAllEvents')
        this.$router.push('/')
      }
    }
  }
</script>



